<template>
  <div>
      <el-checkbox
        v-for="(item, index) in checkeds"
        :key="index"
        @change="changeSelect"
        >{{ item }}</el-checkbox
      >
      <el-button type="primary" @click="save">保存</el-button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      checkeds: ["选择1", "选择2", "选择3", "选择4", "选择5", "选择6"],
      all:[]
    };
  },
  methods: {
    save() {
      if (this.all.includes(true)) {
        alert(" 发1请求");
      } else {
        alert("发0请求");
      }
    },
    changeSelect(e) {
        if(e){
            this.all.push(e)
            console.log(this.all);
        }else{
            this.all.splice(0,1)
            console.log(this.all);
        }
    },
  },
};
</script>

